* {
  box-sizing:border-box;
  outline: none;
}

body {
  margin: 0;
  min-width: 320px;
  font-family: $font;
  font-size: 16px;
  height: 100%;
  color: $base-color;
}

.container {
  max-width: 1400px;
}

.wrap-reverse {
  flex-wrap: wrap-reverse;
}

a {
  text-decoration:none;
  color: $base-color;

  &:hover,
  &:active {
    text-decoration: none;
    color: rgba($base-color, 0.5);
  }
}

img {
  max-width:100%;
  height:auto;
}

.wrap-page {
  max-width: 1920px;
  width: 100%;
  margin: 0 auto;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  letter-spacing: 1px;
  padding-top: 90px;
  min-height: 100vh;

  .main {
    flex: 1 0 auto;
  }
}

.btn {
  display: inline-block;
  text-decoration: none;
  padding: 15px 25px;
  text-transform: uppercase;
  width: max-content;
  transition: all .2s ease-in-out;
  border-radius: 0;
  box-shadow: none;

  &--white {
    color: #fff;
    border: 1px solid #fff;

    &:hover {
      background-color: #fff;
      color: #000;
    }
  }

  &--black {
    background-color: #000;
    border: 1px solid #000;
    color: #fff;

    &:hover {
      background-color: transparent;
      color: #000;
    }
  }
}

.hidden-xs {
  @media screen and (max-width: $xs){
    display: none;
  }
}